* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.logo {
  width: 300px;
}
.header {
  padding: 15px;
  background: #f0f0f0;
}
.card {
  transition: all 0.5s ease-in;
  height: 32rem;
}
.card:hover {
  transform: translateY(-1%);
}
.card-img {
  display: flex;
  margin: auto;
  width: 12rem;
  height: 10rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.card-btn {
  display: block;
  background-color: #ef7f1a;
  color: #fff;
  outline: none;
  border: none;
  padding: 0.8rem;
  cursor: pointer;
  border-radius: 1rem;
  font-size: 1rem;
  transition: all 0.3s ease-in-out;
}
.card-btn:hover {
  color: #ef7f1a;
  background-color: #fff;
  border: solid 1px #ef7f1a;
}
.count-btn {
  color: #ef7f1a;
  background-color: #fff;
  border-radius: 0.5rem;
  border: solid 1px #ef7f1a;
  padding: 0.5rem;
  width: 3rem;
  font-size: 1.5rem;
}
.count-btn:hover {
  background-color: #ef7f1a;
  color: #fff;
  outline: none;
  border: none;
}
.left-menu-img {
  width: 3rem;
  height: 2rem;
  margin-right: 0.5rem;
}
.link {
  text-decoration: none;
}
.slide-img {
  width: 100%;
  height: 450px;
}
.btn-link {
  text-decoration: none;
}
.form {
  padding-left: 2rem;
}
.counter-input {
  display: block;
  width: 2.5rem;
  height: 2rem;
  font-size: 1.2rem;
  text-align: center;
  border-radius: 0.5rem;
  border: solid 1px #1976d2;
}
.password-field {
  position: relative;
}
.passwod-icon {
  position: absolute;
  right: 0;
}
.cart-img {
  display: flex;
  margin: auto;
  width: 5rem;
  max-height: 5rem;

  margin-top: 1rem;
  margin-bottom: 1rem;
}
.cart-count-btn {
  color: #ef7f1a;
  background-color: #fff;
  border-radius: 0.2rem;
  border: solid 1px #ef7f1a;
  padding: 0.2rem;
  width: 1rem;
  font-size: 0.8rem;
  cursor: pointer;
  margin: 0.2rem;
}
.search-li {
  list-style: none;
}
.admin-link {
  color: #fff;
  text-decoration: none;
}
